import React, { useState } from "react";
import { Link as RouterLink } from "react-router-dom";
import { Grid, Link, IconButton } from "@material-ui/core";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import clsx from "clsx";
import Typography from "@material-ui/core/Typography";
import Produto from "../../components/produto";
import ChevronLeftRoundedIcon from "@material-ui/icons/ChevronLeftRounded";
import ChevronRightRoundedIcon from "@material-ui/icons/ChevronRightRounded";
import SwipeableViews from "react-swipeable-views";

export default function FoliaCarousel(props) {
  const { produtos, titulo } = props;

  const theme = useTheme();
  theme.spacing(0);
  const useStyles = makeStyles(theme => ({
    h5: {
      color: "#4A4A4A!important",
      marginBottom: 0,
      marginLeft: "10px"
    },
    textRight: {
      textAlign: "right"
    },
    iconbutton: {
      padding: 0
    }
  }));
  const classes = useStyles();

  const styles = {
    root: {
      padding: "0 5px"
    },
    slideContainer: {
      padding: "0 5px",
      width: "auto"
    },
    slide: {
      maxWidth: "35vw",
      maxHeight: "20vw",
      overflow: "hidden"
    },
    margins: {
      margin: "5px"
    }
  };

  const [slideIndex, setSlideIndex] = useState(0);

  function slidePrevious() {
    let newindex = slideIndex - 1 >= 0 ? slideIndex - 1 : produtos.length - 1;
    setSlideIndex(newindex);
  }

  function slideNext() {
    let newindex = slideIndex + 1 < produtos.length ? slideIndex + 1 : 0;
    setSlideIndex(newindex);
  }

  return (
    <Grid container spacing={1}>
      <Grid item xs={6}>
        <Typography variant="h5" gutterBottom className={classes.h5}>
          {titulo}
        </Typography>
      </Grid>
      <Grid item xs={6}>
        <Typography
          variant="h5"
          gutterBottom
          className={clsx(classes.h5, classes.textRight)}
        >
          <IconButton
            aria-label="anterior"
            onClick={slidePrevious}
            className={classes.iconbutton}
          >
            <ChevronLeftRoundedIcon fontSize="large" />
          </IconButton>
          <IconButton
            aria-label="prÃ³ximo"
            onClick={slideNext}
            className={classes.iconbutton}
          >
            <ChevronRightRoundedIcon fontSize="large" />
          </IconButton>
        </Typography>
      </Grid>
      <Grid item xs={12}>
        <SwipeableViews
          style={styles.root}
          slideStyle={styles.slideContainer}
          index={slideIndex}
        >
          {produtos.map(item => (
            <div key={item.id} style={Object.assign({}, styles.slide)}>
              <Link to="/compre-aqui" component={RouterLink}>
                <Produto dados={item} />
              </Link>
            </div>
          ))}
        </SwipeableViews>
      </Grid>
    </Grid>
  );
}
