import React from "react";
import { useSelector } from "react-redux";
import * as _ from "lodash";
import FoliaCarousel from "./FoliaCarousel";
import { Grid, Slide } from "@material-ui/core";
import FoliaBanner from "../../components/foliabanner";

const styles = {
  gridcontainer: {
    marginTop: "10px"
  },
  griditem: {
    marginTop: "5px",
    marginBottom: "5px"
  }
};
export default function Home() {
  let camarotes = useSelector(state => state.products.camarotes);
  camarotes = _.filter(camarotes, ["type", "grouped"]);
  let blocos = useSelector(state => state.products.blocos);
  blocos = _.filter(blocos, ["type", "grouped"]);
  const passaportes = useSelector(state => state.products.passaportes);

  return (
    <div>
      <FoliaBanner />
      <Slide direction="up" in={true} mountOnEnter unmountOnExit>
        <Grid container spacing={3} style={styles.gridcontainer}>
          <Grid item xs={12} style={styles.griditem}>
            <FoliaCarousel titulo="CAMAROTES" produtos={camarotes} />
          </Grid>
          <Grid item xs={12} style={styles.griditem}>
            <FoliaCarousel titulo="BLOCOS" produtos={blocos} />
          </Grid>
          <Grid item xs={12} style={styles.griditem}>
            <FoliaCarousel
              titulo="PASSAPORTE DA FOLIA"
              produtos={passaportes}
            />
          </Grid>
        </Grid>
      </Slide>
    </div>
  );
}
